import { useState } from "react"
import { RegisterScreen } from "./register"
import { LoginScreen } from "./login"
import React from "react"
import { Button, Card, Divider, Typography } from "antd"
import styled from "@emotion/styled"
import logo from '../assets/logo.svg'
import right from '../assets/right.svg'
import left from '../assets/right.svg'


export const UnauthenticatedApp=()=>{
  //切换登录和注册页面
  const [isRegister,setIsRegister]=useState(false)
  const [error,setError]=useState<Error|null>(null)

  //什么时候error有值？RegisterScreen 以及 LoginScreen抛出错误的时候

  return <Container style={{display:'flex',justifyContent:'center'}}>
    <Header/>
    <Backgroud/>
   
    <ShaddowCrad>
      <Title>{isRegister?'请注册':'请登录'}</Title>
      {error?<Typography.Text type={"danger"}>{error.message}</Typography.Text>:null}
      {isRegister?<RegisterScreen onError={setError}/>:<LoginScreen onError={setError}/>}
      <Divider/>
      <Button type={'link'} onClick={()=>{setIsRegister(!isRegister)}}>
        {isRegister?'已有账号？请登录':'没有账号？注册新账号'}</Button>
    </ShaddowCrad>
   

  </Container>
}
 
export const LongButton=styled(Button)`
  width: 100%;
`

const Title=styled.h2`
  margin-bottom:2.4rem;
  color: rgb(94,108,132);
`


const Backgroud =styled.div`
  position:absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  /* 是否会随着页面的滑动而滑动 */
  background-attachment: fixed;
  background-position: left bottom,right bottom;
  background-size:calc(((100vw-40rem)/2)-3.2rem),calc(((100vw-40rem)/2)-3.2rem),cover;
  background-image:url(${left}), url(${right});
`

const Header=styled.header`
  background:url(${logo}) no-repeat center;
  padding:5rem 0;
  background-size:8rem;
  width:100%;
`

const ShaddowCrad=styled(Card)`
  width:40rem;
  min-height:56rem;
  padding:3.2rem 4rem;
  border-radius:0.3rem;
  box-sizing:border-box;
  box-shadow:rgba(0,0,0,0.1) 0 0 10px;
  text-align:center;
`

const Container=styled.div`
  display:flex;
  flex-direction:column;
  align-items: center;
  min-height:10vh;


`