import React from 'react'
import { User } from '../../types/user'
import { Dropdown, Menu, Modal, Table } from 'antd'
import { TableProps } from "antd/es/table"
import dayjs from 'dayjs'
//react-router和react-router-dom的关系，
import {Link} from 'react-router-dom'
import { Pin } from '../../components/Pin'
import { useDeleteProject, useEditProject } from '../../util/project'
import { ButtonNoPadding } from '../../components/lib'
import { useProjectModal, useProjectsQueryKey } from './util'
import { Project } from '../../types/project'


interface ListProps extends TableProps<Project>{
  // list:Project[],
  users:User[],
  // refresh?:()=>void,
}

export const List=({users,...props}:ListProps)=> {
  const {mutate}=useEditProject(useProjectsQueryKey())
  // const dispatch=useDispatch()
  const pinProject=(id:number)=>(pin:boolean)=>mutate({id,pin})

  
 
  return (
    <Table
      rowKey={"id"}
      pagination={false}
      
      columns={[
        {
          title:<Pin checked={true} disabled={true}/>,
          render(value,project){
            return <Pin checked={project.pin} onCheckedChange={pinProject(project.id)}></Pin>
          }
        },
        {  
          title:'名称',
          dataIndex:'name',
          sorter:(a,b)=>a.name.localeCompare(b.name),
          render(value, project) {
            
            return <Link to={String(project.id)}>{project.name}</Link>;
          },
        },
        {
          title:'部门',
          dataIndex:'organization',
          sorter:(a,b)=>a.name.localeCompare(b.name)
        },
        {
          title:'负责人',
          render(value,project){
            return <span>
              {users.find((user:User)=>user.id===project.personId)?.name||'未知'}
            </span>
          }
        },
        {
          title:'创建时间',
          render(value,project){
            return <span>
              {project.created?dayjs(project.created).format('YYYY-MM-DD'):'无'}
            </span>
          }
        },
         {
          render(value,project){
            return <More project={project}></More>
          }
         }
      ]}
      
      {...props}
    />
     
  )
}

const More=({ project}:{project:Project})=>{
  const {startEdit}=useProjectModal()
  const editProject=(id:number)=>()=>startEdit(id)
  const {mutate:deleteProject}=useDeleteProject(useProjectsQueryKey())
  
  const confirmDeleteProject=(id:number)=>{
    Modal.confirm({
      title:'确定删除这个项目吗',
      content:'点击确定删除',
      okText:'确定',
      onOk(){
        deleteProject({id})
      }
    })
  }

   return <Dropdown overlay={<Menu>
      <Menu.Item key={'edit'}>
        <ButtonNoPadding
        type={'link'}
      //  onClick={()=>dispatch(prejectListActions.openProjectModal())}
        onClick={editProject(project.id)}
        >
        编辑
        </ButtonNoPadding>
      </Menu.Item>

      <Menu.Item key={'delete'}>
        <ButtonNoPadding
        type={'link'}
         onClick={()=>confirmDeleteProject(project.id)}
        >
        删除
        </ButtonNoPadding>
      </Menu.Item>

    </Menu>}>
      <ButtonNoPadding type={"link"}>...</ButtonNoPadding>
    </Dropdown>
  
}
