import React from "react";
import { Raw } from "../types";
import { Select } from "antd";

//获得Select的组件的所有属性类型
type Selectprops=React.ComponentProps<typeof Select>

//子与父发生了冲突需要Omit
interface IdSelectProps extends Omit<Selectprops,'value'|'onChange'|'options'>{
  value?:Raw|null|undefined,//value能处理的类型
  onChange?:(value?:number)=>void,
  defaultOptionName?:string,
  options?:{name:string,id:number}[],

}

/**
 * value可以传入多种类型的值
 * onChange只会回调number|undefined类型，值为0会回调undefined
 * 当isNaN(number(vaue))为true的时候，代表选择默认类型
 * 当选择默认类型的时候，onChange会回调undefined
 * 
 */

export const IdSelect=(props:IdSelectProps)=>{
  const {value,onChange,defaultOptionName,options,...restProps}=props
  return <Select
 
  value={options?.length?toNumber(value):0}
  onChange={(value)=>onChange?.(toNumber(value)||undefined)}
  {...restProps}
  >
    {
      defaultOptionName?(<Select.Option value={0}>{defaultOptionName}</Select.Option>):null
    }
    {
      options?.map(option =><Select.Option key={option.id} value={option.id}>{option.name} </Select.Option>)
    } 
  </Select>
}
//将输入值转换为数字
//value 是无法被转换为数字的值（如字符串 "abc"、null、undefined 或对象），Number(value) 会返回 NaN（Not-a-Number）。

const toNumber=(value:unknown)=>(isNaN(Number(value))?0:Number(value))