import React, { } from 'react'
import {List} from './List'
import SearchPanel from './SearchPanel'
import { useDebounce, useDocumentTitle,} from '../../util/index'
import styled from '@emotion/styled'
import {Typography } from 'antd'
import { useProjects } from '../../util/project'
import { useUsers } from '../../util/user'
import { useProjectModal, useProjectSearchParams } from './util'
import { ButtonNoPadding, Row } from '../../components/lib'
// import { useDispatch } from 'react-redux'
// import { prejectListActions } from './projectList.slice'


export const ProjectListScreen=()=> {


  //基本类型、组件状态可以放到依赖里；非组件状态的对象绝不可以放到依赖里
 useDocumentTitle('项目列表',false)
  
  const [param,setParam]=useProjectSearchParams()
  const {isLoading,error,data:list}=useProjects(useDebounce(param,200))
  const {data:users}=useUsers()
  // const dispatch=useDispatch()
  const {open}=useProjectModal()
  

  return (
    <Container>
      <Row between={true}>
        <h1>项目列表</h1>
        <ButtonNoPadding
        // onClick={()=>dispatch(prejectListActions.openProjectModal())}
        onClick={open}
        type={'link'}
        >
          创建项目
        </ButtonNoPadding>
      </Row>
      <SearchPanel param={param}  setParam={setParam} users={users||[]}></SearchPanel>
      {error?<Typography.Text type={'danger'}>{error.message}</Typography.Text>:null}
      <List
      // refresh={retry} 
      loading={isLoading} 
      users={users || []} 
      dataSource={list || []}>
      </List>
      
    </Container>
  )
}

ProjectListScreen.whyDidYouRender=false

const Container =styled.div`
  width: 100%;
  padding:3.2rem;
`