// import { useEffect } from "react"
import { Project } from '../types/project';
// import { useAsync } from "./useAsync"
// import { cleanObject } from "../util/index"
import { useHttp } from "./http"
import { QueryKey, useMutation, useQuery,  } from "react-query"
import { useAddConfig, useDeleteConfig, useEditConfig } from "./useOptimisticOptions"
import { cleanObject } from "../util/index";


//主要处理组件发送请求得到project的数据
//传入搜索的对象,因为搜索的信息是Project的一部分
export const useProjects = (param?: Partial<Project>) => {
  const client = useHttp();

  return useQuery<Project[]>(["projects", cleanObject(param)], () =>
    client("projects", { data: param })
  );
};

export const useEditProject = (queryKey: QueryKey) => {
  //发送请求的自定义hook
  const client = useHttp();
  return useMutation(
    (params: Partial<Project>) =>
      client(`projects/${params.id}`, {
        method: "PATCH",
        data: params,
      }),
    useEditConfig(queryKey)
  );
};

export const useAddProject = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation(
    (params: Partial<Project>) =>
      client(`projects`, {
        data: params,
        method: "POST",
      }),
    useAddConfig(queryKey)
  );
};

export const useDeleteProject = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation(
    ({ id }: { id: number }) =>
      client(`projects/${id}`, {
        method: "DELETE",
      }),
    useDeleteConfig(queryKey)
  );
};


//获取project详情
//需要区别，在没有参数传入时，是否有操作？有的在没有参数传入的时候，是返回所有的数据
//有的功能是在没有参数传入的时候，则不会有任何操作
export const useProject = (id?: number) => {
  const client = useHttp();
  return useQuery<Project>(
    ["project", { id }],
    () => client(`projects/${id}`),
    {
      enabled: Boolean(id),
    }
  );
};

