
import { useEffect, useRef, useState } from "react"



//判断是否为0
//isFalsy 函数的目的是判断一个值是否为“假值”，但同时将数字 0 视为“真值”。
//如果 value 等于 0，返回 false。这是因为 0 在 
// JavaScript 中是一个假值，但在这里被特殊处理为“真值”。
 export const isFalsy=(value:unknown)=>value===0?false:!value

 export const isVoid=(value:unknown)=>value===undefined||value===null||value===''

 //使用 Object.keys(object) 
 // 获取对象的所有键名，并通过 forEach 遍历每个键。
export const cleanObject =(object?:{[key:string]:unknown})=>{
  if(!object){
    return {}
  }
  const result={...object}
  Object.keys(result).forEach(key=>{ 
    //0
    const value=result[key]
    if(isVoid(value)){
      delete result[key]
    }
  })
  return result
}

export const useMount=(callback:()=>void)=>{
  useEffect(()=>{
    callback()
    // TODO 依赖项里加上callback会造成无限循环，这个和useCallback以及useMemo有关系
    // eslint-disable-next-line react-hooks/exhaustive-deps
  },[])
}
//用泛型来规范类型
export const useDebounce=<V>(value:V,delay?:number)=>{
  const [debounceValue,seteDbounceValue]=useState(value)
  useEffect(()=>{
    //每次value变化后，设置一个定时器
    const timeout=setTimeout(()=>seteDbounceValue(value),delay)
    //每次在上一个useEffect处理完以后运行
    return ()=>{clearTimeout(timeout)}
  },[value,delay])
  return debounceValue
}


export const useDocumentTitle=(title:string,keepOnmount=true)=>{
  const oldTitle=useRef(document.title).current
  //页面加载时，旧title
  //加载后 新title

  useEffect(()=>{ 
     document.title=title
  },[title])

  useEffect(()=>{
    return ()=>{
      if(!keepOnmount){
        document.title=oldTitle
      }
    }
  },[keepOnmount,oldTitle])
}

export const resetRoute=()=>window.location.href=window.localStorage.origin

/*
  返回组件得挂载状态，如果还没挂载或者以及卸载，返回false，反之返回true

*/
export const useMountedRef=()=>{
  const mountedRef=useRef(false)

  useEffect(()=>{
    mountedRef.current=true
    return ()=>{
      mountedRef.current=false
    }
  })
  return mountedRef
}

/**
 * 传入一个对象，和键集合，返回对应的对象中的键值对
 * @param obj
 * @param keys
 */
export const subset = <
  O extends { [key in string]: unknown },
  K extends keyof O
>(
  obj: O,
  keys: K[]
) => {
  const filteredEntries = Object.entries(obj).filter(([key]) =>
    keys.includes(key as K)
  );
  return Object.fromEntries(filteredEntries) as Pick<O, K>;
};