import React from 'react'
import { useProjects } from '../util/project'
import {  Divider, List, Popover, Typography } from 'antd'
import styled from "@emotion/styled"
// import { useDispatch } from 'react-redux'
import { ButtonNoPadding } from './lib'
// import { prejectListActions } from '../screens/projectList/projectList.slice'
import { useProjectModal } from '../screens/projectList/util'
 

export const ProjectPopover=()=>{
  // const dispatch=useDispatch()
  const {open}=useProjectModal()

  const {data:projects,refetch}=useProjects()
  //筛选收藏项目 
  const pinnedProjects=projects?.filter(project=>project.pin)

  const content =<ContentContainer>
    <Typography.Text type={'secondary'}>收藏项目</Typography.Text>
    <List>
      {
        pinnedProjects?.map(project=><List.Item key={project.id}>
          <List.Item.Meta title={project.name}></List.Item.Meta>
        </List.Item>)
      }
    </List>
    <Divider/> 
    <ButtonNoPadding
      // onClick={()=>dispatch(prejectListActions.openProjectModal())}
      onClick={open}
      type={'link'}
    >
      创建项目
    </ButtonNoPadding>
  </ContentContainer>

  return <Popover
      placement={"bottom"} 
      content={content}
      onOpenChange={()=>refetch()}
      >
    <span>项目</span>
  </Popover>
}

const ContentContainer=styled.div`
  min-width:30rem
`