import { useCallback, useReducer, useState } from "react"
import { useMountedRef } from "."

interface State<D>{
  error:Error|null,
  data:D|null,
  stat:'idle'|'loading'|'error'|'success'
}

//默认的state
const defaultInitialState: State<null>={
  stat:'idle',
  data:null,
  error:null
}
//抛出异常可选
const defaulConfig={
  throwOnError:false
}

const useSafeDispatch=<T>(dispatch:(...args:T[])=>void)=>{
  //判断组件是否已经挂载到 DOM 上
  const mountedRef=useMountedRef()
  //使用useSafeDispatch的原因是在useSafeDispatch中作判断组件是否挂载完毕
  return useCallback((...args:T[])=>(mountedRef.current?dispatch(...args):void 0),[dispatch,mountedRef])
}

//用户传入的state，优先级比默认高
export const useAsync=<D>(initialState?:State<D>,initialConfig?:typeof defaulConfig)=>{
  const config={...defaulConfig,initialConfig}
  const [state,dispatch]=useReducer((state:State<D>,action:Partial<State<D>>)=>({...state,...action}),{
    ...defaultInitialState,
    ...initialState
  })

  //利用useSate的惰性初始化，将一个状态保存为一个函数
  const [retry,setRetry]=useState(()=>()=>{})
  //
  const safeDispatch=useSafeDispatch(dispatch)

  //当调用setData时说明请求成功
  const setData = useCallback(
    (data: D) =>
      safeDispatch({
        data,
        stat: "success",
        error: null,
      }),
    [safeDispatch]
  );

  const setError = useCallback(
    (error: Error) =>
      safeDispatch({
        error,
        stat: "error",
        data: null,
      }),
    [safeDispatch]
  );

  //触发异步请求
  const run=useCallback(
    (promise:Promise<D>,runConfig?:{retry:()=>Promise<D>})=>{
    //不是promise
    if(!promise||!promise.then){
      throw new Error('输入Promise类型数据')
    }
 
    setRetry(()=>()=>{
      if(runConfig?.retry){
        run(runConfig?.retry(),runConfig)
      }
    })

    safeDispatch({stat:'loading'})
    return promise.then(data=>{
      setData(data) 
      return data
    }).catch(error=>{
      //catch会消化异常，如果不主动抛出，外面是接收不到异常的
      setError(error)
      if(config.throwOnError){
        return Promise.reject(error)
      }
      return error
      
    })
  }
  ,[config.throwOnError, safeDispatch, setData, setError])


  return {
    isIdle:state.stat==='idle',
    isLoading:state.stat==='loading',
    isError:state.stat==='error',
    isSuccess:state.stat==='success',
    run,setData,setError,...state,retry
  }

}
