import { Button, Drawer, Form, Input, Spin } from "antd";

import React, { useEffect } from "react";
// import { useDispatch, useSelector } from "react-redux";
// import { prejectListActions, selectProjectModalOpen } from "./projectList.slice";
import { useProjectModal, useProjectsQueryKey } from "./util";
import { UserSelect } from "../../components/userSelect";
import { useAddProject, useEditProject } from "../../util/project";
import { useForm } from "antd/lib/form/Form";
import { ErrorBox } from "../../components/lib";
import styled from "@emotion/styled"


export const ProjectModal=()=>{
    // const dispatch=useDispatch()
    // const projectModalOpen=useSelector(selectProjectModalOpen)
  const {projectModalOpen,close,isLoading,editingProject}=useProjectModal()
  const title=editingProject?'编辑项目':'创建项目'

  const useMutateProject=editingProject?useEditProject:useAddProject
  const [form]=useForm()
  //希望完成编辑或创建项目后再触发关闭等功能
  const {mutateAsync,error,isLoading:mutateLoading}=useMutateProject(useProjectsQueryKey())

  const onFinish=(values:any)=>{
    mutateAsync({...editingProject,...values}).then(()=>{
      form.resetFields()
      close()
    })
  }
  //表单重置一遍
  useEffect(()=>{
    form.setFieldsValue(editingProject)
  },[editingProject,form])
  
  //在退出关闭后，表单的数据清空
  const closeModal=()=>{
    form.resetFields()
    close()
  }

  return <Drawer 
    // onClose={()=>dispatch(prejectListActions.closeProjectModal())}
    forceRender={true}
    onClose={closeModal}
    open={projectModalOpen}
    width={"100%"}
  >
    <Container>
       {
        isLoading?<Spin size={"large"}/>:<>
        <h1>{title}</h1>
        <ErrorBox error={error}></ErrorBox>
        <Form form={form} layout={'vertical'} style={{width:'40rem'}} onFinish={onFinish}>
          <Form.Item label={'名称'} name={'name'} rules={[{required:true,message:'请输入项目名'}]}>
            <Input placeholder={"请输入项目名称"}></Input>
          </Form.Item>
          <Form.Item label={'部门'} name={'organization'} rules={[{required:true,message:'请输入部门名'}]}>
            <Input placeholder={"请输入部门名称"}></Input>
          </Form.Item>
          <Form.Item label={'负责人'} name={'personId'} >
            <UserSelect defaultOptionName={'负责人'} ></UserSelect>
          </Form.Item>
          <Form.Item>
            <Button loading={mutateLoading} type={"primary"} htmlType={"submit"}>
              提交
            </Button>
          </Form.Item>
        </Form>
      </>
    }
    </Container>
   

  </Drawer>
}

const Container=styled.div`
  height: 80vh;
  display:flex;
  justify-content:center;
  align-items:center;
  flex-direction:column;
`